






































































import loading from 'template/loading'
import goodsCard from 'template/goods_card'
import http from 'utils/http.js';
import COOKIE from 'utils/cookie.js';
export default {
	components: {
		goodsCard,
		loading
	},
	data() {
		return {
			type: 1,
			num: 1,
			showNum: false,
			content: '',
			imgpath: [],
			info: {},
			contact_info: {},
			applyimg: [],
			isLoading: false,
			apptype: [{
					id: 1,
					name: '换货',
					img_sel: '/static/img/exchange_sel.png',
					img_nor: '/static/img/exchange_nor.png'
				},
				{
					id: 2,
					name: '退货',
					img_sel: '/static/img/return_sel.png',
					img_nor: '/static/img/return_nor.png'
				}
			]
		};
	},
	onLoad(options) {
		this.id = options.id
		this.getInfo()
	},
	methods: {
		currentTab(id) {
			this.type = id
			console.log(this.type)
		},
		//接收子组件传递的数量
		setGoodsNum(data) {
			this.num = data.num
			if (this.num > this.info.number) {
				this.num = this.info.number
				return uni.showToast({
					title: "已超过订单商品数量",
					icon: "none"
				})
			}
		},

		getInfo() {
			var that = this
			this.$api.get('/api/aftersale/getDataToApply', {
				order_goods_id: this.id
			}, function(res) {
				if (res.data.code == 200) {
					that.info = res.data.data
					if (res.data.data.number > 1) {
						that.num = res.data.data.number
						that.showNum = true
					} else {
						that.info.num = res.data.data.number
					}
					that.contact_info = res.data.data.contact_info
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
				console.log(res)
			})
		},
		//选择图片
		choose_img: function(e) {
			var that = this
			var imgs = this.imgpath
			var applyimg = this.applyimg
			if (applyimg.length >= 5) {
				return uni.showToast({
					title: '最多选择五张照片~',
					icon: 'none'
				})
			}
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
					var tempFilePaths = res.tempFilePaths
					that.upimg(tempFilePaths[0], tempFilePaths)
				}
			})
		},
		//上传图片
		upimg: function(uppath, showTemp) {
			this.isLoading = true
			var cookie = COOKIE.getUserCookie();
			var cookestr = ''
			if (cookie) {
				var c = 0
				for (var i in cookie) {
					if (c !== 0) {
						cookestr += " "
					}
					cookestr += i + '=' + cookie[i];
					c++;
				}
			}
			var that = this
			var imgs = this.imgpath
			var list = this.applyimg
			uni.uploadFile({
				url: http.baseUrl + '/api/common/upload',
				filePath: uppath,
				name: 'file', //文件对应的参数名字(key)
				header: {
					'Cookie': cookestr
				},
				success: function(res) {
					console.log(res)
					var result = JSON.parse(res.data);
					console.log(result)
					if (result.code == 200) {
						// list = list + ',' + (result.data.url)
						that.isLoading = false
						imgs.push(showTemp)
						that.imgpath = imgs
						list.push(result.data.url)
						that.applyimg = list
						console.log(list)
					}
				}
			})
		},

		//删除图片
		delete_img(index) {
			console.log(this.applyimg[index])
			this.$api.post('/api/common/delImage', {
				image_url: this.applyimg[index]
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {}
			})
			var applyimg = this.applyimg
			var imgpath = this.imgpath
			applyimg.splice(index, 1)
			imgpath.splice(index, 1)
			this.applyimg = applyimg
			this.imgpath = imgpath
		},

		//获取文本
		textdown: function(e) {
			this.content = e.detail.value
		},

		//提交
		submit: function() {
			if (this.content == '') {
				return uni.showToast({
					title: '请填写申请原因',
					icon: 'none'
				})
			}
			var that = this
			var data = {
				order_goods_id: this.id,
				number: this.num,
				type: this.type,
				reason: this.content,
				images: this.applyimg.join(','),
			}
			uni.showLoading({
				title: '正在提交',
			})
			this.$api.post('/api/aftersale/submitApply', data, function(res) {
				console.log(res)
				uni.hideLoading()
				if (res.data.code == 200) {
					uni.showToast({
						title: '提交成功！',
						duration: 1000,
						mask: true,
					})
					var pages = getCurrentPages();
					var prevPage = pages[pages.length - 2];
					prevPage.$vm.upDate = true
					setTimeout(function() {
						uni.navigateBack()
					}, 1000)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none",
						duration: 1000,
						mask: true,
					})
				}
			})
		}
	}
}
